h1 {
  text-align: center;
}

.box {
  width: 30rem;
  height: 20rem;
  margin: 0 auto;
  font-size: 1.5rem;
  display: flex;
  .move {
    display: block;
    width: 6rem;
    height: 4rem;
    text-decoration: none;
    color: black;
    border: 0.2rem solid #000;
    text-align: center;
    margin: 1rem;
    box-sizing: border-box;
  }
}
